.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #000; /* Couleur de fond noire */
    color: #fff; /* Texte en blanc */
    padding: 1rem;
    bottom: 0;
    height:209px;
    
    .footer-logo {
      height: 40px; /* Ajustez la taille du logo */
      margin-right: 1rem;
      padding: 30px;
    }
  
    .footer-text {
      margin: 0;
      font-size: 1.5rem; /* Ajustez la taille de la police si nécessaire */
    }
  }
  @media (max-width: 768px) {
    .footer{

    .footer-text{
      font-size: 14px;
    }
  }
}